import React, { useState, useRef } from "react";

const Search = () => {
  const [find, setFind] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    console.log(value)
    setFind({...find,[name]:value})
  };

  return (
    <div class="flex justify-center mt-2">
      <div class="mb-3 xl:w-96">
        <form
          class=" input-group relative flex items-stretch h-8  w-full mb-4 opacity-50"
          onSubmit={handleSubmit}
        >
          <input
            type="search"
            name="search"
            onChange={handleChange}
            class="rounded-l-full rounded-r-none form-control border-r-0 relative flex-auto min-w-0 block w-full px-3 py-1.5 text-base font-normal text-black bg-white bg-clip-padding border border-solid border-gray-dark rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-red-dark focus:outline-none"
            placeholder="Search"
            aria-label="Search"
            aria-describedby="button-addon2"
          />
          <button
            class="btn border-l-0 rounded-l-none rounded-r-full px-6 py-2.5 bg-red text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-red-dark hover:shadow-lg   focus:shadow-lg focus:outline-none focus:ring-0  active:shadow-lg transition duration-150 ease-in-out flex items-center"
            type="button"
            id="button-addon2"
          >
            <svg
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="search"
              class="w-4"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 512 512"
            >
              <path
                fill="currentColor"
                d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"
              ></path>
            </svg>
          </button>
        </form>
      </div>
    </div>
  );
};

export default Search;
